import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function";
import { NotifInfo, ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { postData } from "../../../../config/axios";
import * as moment from "moment-timezone";

const HeadFormLaporanPembelianSales = lazy(() =>
  import("./HeadFormLaporanPembelianSales")
);

class FormLaporanPembelianSales extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanTransaksiPembelian: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
      kode_sales : data.kode_sales,
      jenis_group: data.jenis_group,
      kondisi: data.kondisi,
    };

    postData("pembelian/get/reportsales", data_post)
      .then((res) => {
        console.log(res.data.length);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          // this.setState({ isLoading: false });
          this.setState({
            export: false,
            isLoading: false,
            LaporanTransaksiPembelian : []
          });
        } else {
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            LaporanTransaksiPembelian : []
          });
          this.setState({
            export: true,
            isLoading: false,
            LaporanTransaksiPembelian : res.data
          });
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }

  errorHandling(err) {
    NotifInfo(err)
    this.setState({ isLoading: false });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Pembelian Sales</li>
        </ol>
        <h1 className="page-header">Laporan Pembelian Sales </h1>
        <Panel>
          <PanelHeader>Laporan Pembelian Sales</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanPembelianSales
              isLoading={this.state.isLoading}
              export={this.state.export}
              LaporanTransaksiPembelian={this.state.LaporanTransaksiPembelian}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

export default FormLaporanPembelianSales;
